<template>
  <div>
    <!---->
    <el-dialog :close-on-click-modal="false" title="" :visible="true" class="dialogForm" width="1300px" @close="closeDialog">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="" size="mini" prop="company_id">
            {{(function(){if(params.company_id == 0)params.company_id = null})()}}
            <el-select v-model="params.company_id" placeholder="" filterable disabled="disabled" @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="" end-placeholder="" clearable>
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-input v-model="params.company_appraisal_sn" class="shortSearchInput"></el-input>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-select v-model="params.customer_id" filterable placeholder="" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
              <ul class="customer customer-ul4 customer-ul2">
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
              <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
                <ul class="customer customer-ul4">
                  <li :title="item.customer_id">{{ item.customer_id }}</li>
                  <li :title="item.customer_name">{{ item.customer_name }}</li>
                  <li :title="item.contact_name">{{item.contact_name}}</li>
                  <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                  <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
                </ul>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-input v-model="params.tire_un" class="shortSearchInput"></el-input>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-input v-model="params.goods_id" class="shortSearchInput"></el-input>
          </el-form-item>
          <el-form-item label="" size="mini">
            <el-input v-model="params.goods_name" class="shortSearchInput"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary"></el-button>
            <el-button plain @click="onReset" size="mini"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- <p class="dialogTips"></p><br/> -->
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1" max-height="450">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_appraisal_sn" width="145" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="tire_un" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_status" label="">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber"></div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="[30, 50, 100,500]" layout="total,sizes, prev, pager, next,jumper" prevText="  " nextText=" " :total="TableData.total ">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer ">
        <el-button @click="closeDialog() "> </el-button>
        <el-button class="tx " type="primary " @click="dialogSubmitForm() "> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      authority: {},
      radio: "",
      TableData: {
        tableData: []
      },
      companyList: [],
      params: {},
      per_page: [30, 50, 100, 500],
      loading: false,
      searchParams: {},
      goPage: 1, //
      pageSize: 30,
      timeValue: [],
      dialogmultipleSelection: [], //
      goodsList: [],
      options4: [],
      list: []
    };
  },
  props: ["message"],
  methods: {
    handleSelectionChange(val) {
      this.addmultipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    },
    getCustomerListRemote(query) {
      //
      getCustomerList(this, {
        company_id: this.params.company_id,
        customer_name: query
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      getData(this.searchParams);
    },
    onReset() {
      /**   */
      this.$set(this, "params", {});
      this.$set(this.params, "company_id", this.message);
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          company_id: this.message
        },
        TableData: this.TableData, //
        API: "getreferenceAppraisalList", //
        vm: this //this
      });
      getData(this.searchParams);
    },
    dialoghandleSelectionChange(val) {
      this.dialogmultipleSelection = val;
    },
    closeDialog() {
      // 
      this.$parent.goodsAddressVisible = false;
    },
    dialogSubmitForm() {
      //
      let evey = this.addmultipleSelection.every(item => {
        return this.addmultipleSelection[0].customer_id == item.customer_id;
      });
      if (!evey) {
        this.$message({
          message: "",
          type: "warning"
        });
        return false;
      }
      let aay = this.addmultipleSelection.map(item => {
        return item.company_appraisal_goods_id;
      });
      this.$emit("customerId", this.addmultipleSelection[0].customer_id);
      this.$emit("getAllocationArr", aay);
      this.$parent.goodsAddressVisible = false;
    }
  },
  mounted() {
    this.companyListInvok();
    this.$set(this.params, "company_id", this.message);
    this.$set(this, "searchParams", {
      params: {
        company_id: this.message
      }, //1
      TableData: this.TableData, //
      API: "getreferenceAppraisalList", //
      vm: this //this
    });
    getData(this.searchParams); //
  }
};
</script>
